import {WebGLGameShader} from './WebGLGameShader'
import {MathUtilities} from '../math/MathUtilities'
import { Matrix } from '../math/Matrix'
import { Vector3 } from '../math/Vector3'
import { GradientCircle } from '../scene/sprite/GradientCircle';

export class GradientCircleRenderer {
    private shader : WebGLGameShader;
    private vertexTexCoordBuffer : WebGLBuffer;
    private shaderProgramToUse : WebGLProgram;
    private Transform : Matrix;
    private Translate : Vector3;
    private Rotate : Vector3;
    private Scale : Vector3; 
    
    public constructor() {}
    
    public init(webGL : WebGLRenderingContext){
        this.shader = new WebGLGameShader();
        var vertexShaderSource =
            'precision highp float;\n' + 
            'uniform mat4 u_transform;\n' +
            'attribute vec4 a_position;\n' +
            'attribute vec2 a_ValueToInterpolate;\n' +
            'varying vec2 val;\n' +
            'void main() {\n' +
            '   val = a_ValueToInterpolate;\n' +
            '   gl_Position = u_transform * a_position;\n' +
            '}\n';

        var fragmentShaderSource =
            'precision highp float;\n' + 
            'varying vec2 val;\n' +
            'uniform int u_color;\n' +
            'void main() {\n' +
            '   float R = 0.5;\n' + 
            '   float dist = sqrt(dot(val, val));\n' +
            '   float alpha = 1.0;\n' +
            '   if (dist > R) {\n' +
            '       discard;\n' + 
            '   }\n' +
            '   gl_FragColor = vec4(0.0, 0.0, 0.0, alpha);\n' +
            '   if (u_color == 0) {\n' +
            '       gl_FragColor.r = dist;\n' +
            '   }\n' +
            '   if (u_color == 1) {\n'+
            '       gl_FragColor.g = dist;\n' +
            '   }\n' +
            '   if (u_color == 2) {\n'+
            '       gl_FragColor.b = dist;\n' +
            '   }\n' +
            '   if (u_color == 3) {\n'+
            '       gl_FragColor.r = dist;\n' +
            '       gl_FragColor.g = dist;\n' +
            '   }\n' +
            '   if (u_color == 4) {\n'+
            '       gl_FragColor.g = dist;\n' +
            '       gl_FragColor.b = dist;\n' +
            '   }\n' +
            '   if (u_color == 5) {\n'+
            '       gl_FragColor.r = dist;\n' +
            '       gl_FragColor.b = dist;\n' +
            '   }\n' +
            '}\n';
        this.shader.init(webGL, vertexShaderSource, fragmentShaderSource);

        var verticesTexCoords = new Float32Array([
            -0.5,  0.5, 0.0, 0.0,
            -0.5, -0.5, 0.0, 1.0,
             0.5,  0.5, 1.0, 0.0,
             0.5, -0.5, 1.0, 1.0
        ]);

        this.vertexTexCoordBuffer = webGL.createBuffer();
        webGL.bindBuffer(webGL.ARRAY_BUFFER, this.vertexTexCoordBuffer);
        webGL.bufferData(webGL.ARRAY_BUFFER, verticesTexCoords, webGL.STATIC_DRAW);

        this.Transform = new Matrix(4, 4);
        this.Translate = new Vector3();
        this.Rotate = new Vector3();
        this.Scale = new Vector3();
    }

    public renderGCircles(webGL : WebGLRenderingContext, canvasWidth : number, canvasHeight : number, visibleCircle : Array<GradientCircle>){
        this.shaderProgramToUse = this.shader.getProgram();
        webGL.useProgram(this.shaderProgramToUse);

       // AND THEN RENDER EACH ONE
        for (let gCircle of visibleCircle) {
            this.renderGCircle(webGL, canvasWidth, canvasHeight, gCircle);        
        }
    }

    private renderGCircle(webGL : WebGLRenderingContext, canvasWidth : number, canvasHeight : number, gCircle : GradientCircle){
        let Width : number = gCircle.getWidth();
        let Height : number = gCircle.getHeight();
        let XInPixels : number = gCircle.getPosition().getX() + (Width/2);
        let YInPixels : number = gCircle.getPosition().getY() + (Height/2);
        let XTranslate : number = (XInPixels - (canvasWidth/2))/(canvasWidth/2);
        let YTranslate : number = (YInPixels - (canvasHeight/2))/(canvasHeight/2);
        this.Translate.setX(XTranslate);
        this.Translate.setY(-YTranslate);

        let defaultWidth : number = canvasWidth/2;
        let defaultHeight : number = canvasHeight/2;
        let scaleX : number = Width/defaultWidth;
        let scaleY : number = Height/defaultHeight;
        this.Scale.setX(scaleX);
        this.Scale.setY(scaleY);

        MathUtilities.identity(this.Transform);
        MathUtilities.model(this.Transform, this.Translate, this.Rotate, this.Scale); 

        webGL.bindBuffer(webGL.ARRAY_BUFFER, this.vertexTexCoordBuffer);

        let u_transformLocation = webGL.getUniformLocation(this.shaderProgramToUse, 'u_transform');
        webGL.uniformMatrix4fv(u_transformLocation, false, this.Transform.getData());

        let circle_color = gCircle.getColor();
        let u_colorLocation = webGL.getUniformLocation(this.shaderProgramToUse, 'u_color');
        webGL.uniform1i(u_colorLocation, circle_color);

        let positionAttrLocation = webGL.getAttribLocation(this.shaderProgramToUse, 'a_position');
        webGL.vertexAttribPointer(positionAttrLocation, 2, webGL.FLOAT, false, 16, 0);
        webGL.enableVertexAttribArray(positionAttrLocation);

        let valAttrLocation = webGL.getAttribLocation(this.shaderProgramToUse, 'a_ValueToInterpolate');
        webGL.vertexAttribPointer(valAttrLocation, 2, webGL.FLOAT, false, 16, 0);
        webGL.enableVertexAttribArray(valAttrLocation);

        webGL.drawArrays(webGL.TRIANGLE_STRIP, 0, 4);
    }
}