import {SceneObject} from '../SceneObject'

export class GradientCircle extends SceneObject {
    private width : number;
    private height : number;
    private color : number;

    public getWidth(){
        return this.width;
    }

    public getHeight(){
        return this.height;
    }

    public getColor(){
        return this.color;
    }

    public constructor(width : number, height : number, color : number) {
        super();
        this.width = width;
        this.height = height;
        this.color = color;
    }

    public contains(pointX : number, pointY : number) : boolean {
        let spriteWidth = this.width;
        let spriteHeight = this.height;
        let spriteLeft = this.getPosition().getX();
        let spriteRight = this.getPosition().getX() + spriteWidth;
        let spriteTop = this.getPosition().getY();
        let spriteBottom = this.getPosition().getY() + spriteHeight;
        if (    (pointX < spriteLeft)
            ||  (spriteRight < pointX)
            ||  (pointY < spriteTop)
            ||  (spriteBottom < pointY)) {
                return false;
        }
        else {
            return true;
        }
    }

    public toString() : string {
        let c_str = "";
        switch(this.color){
            case 0:
                c_str = "red";
                break;
            case 1:
                c_str = "blue";
                break;
            case 2:
                c_str = "green";
                break;
            case 3:
                c_str = "yellow";
                break;
            case 4:
                c_str = "cyan";
                break;
            case 5:
                c_str = "magenta";
                break;
            default:
                break;
        }

        let summary : string =  "{ position: ("
                            +   this.getPosition().getX() + ", " + this.getPosition().getY() + ") "
                            +   "(color: " + c_str + ") "
        return summary;
    }
}